@import url(https://fonts.googleapis.com/css2?family=Barlow&display=swap);
ul{padding:0;list-style:none}body{font-family:"Barlow", sans-serif;display:flex;justify-content:center;align-items:center;background:linear-gradient(to left, #4b79a1, #283e51);background-size:cover;background-repeat:no-repeat}

.todoCard{display:flex;flex-direction:column;justify-content:space-between;margin:10px auto;padding:20px;height:100px;max-width:800px;border-radius:5px;box-shadow:rgba(149,157,165,0.2) 0 8px 24px;background:linear-gradient(to left, #457fca, #5691c8);color:white;transition:-webkit-transform 0.3s;transition:transform 0.3s;transition:transform 0.3s, -webkit-transform 0.3s}.todoCard:hover{-webkit-transform:scale(1.05);transform:scale(1.05)}

.userInfo{display:flex;justify-content:space-between}

.todoInfo{display:flex;justify-content:space-between}.todoInfo__task{align-self:center;font-size:20px;max-width:65%}.todoInfo__completed{align-self:center}.todoInfo__completed--true{height:35px;width:35px;background-image:url(../../static/media/check-mark.05623645.svg);background-size:contain}

